import socket from "../CLASSES/Socket";
import codes from "../CLASSES/opcode";

class PlayerVillageResponse {
    /** @param {[Number, Number, String,Number,[Number], [], []]} response */
    constructor(response = []) {
        if (!Array.isArray(response)) response = [0, -1, "", 0, [], {}, {}];
        this.id = response[0];
        this.type = response[1];
        this.name = response[2];
        this.loyalty = response[3];
        let list = this.buildings = [];
        let b = response[4];
        for (let i = 0, j = 1; i < b.length; i += 2, j++)
            list.push([j, b[i], b[i + 1]]);
        this.movements = response[5];
        this.troops = response[6];
    }
}
/** @returns {Promise<PlayerVillageResponse>} */
export default function requestPlayerVillage() { return socket.request(codes.PLAYER_VILLAGE); }
socket.defineStructure(codes.PLAYER_VILLAGE, PlayerVillageResponse);